export function checkIsLandscape() {
    if (window.orientation !== undefined) {
        switch(window.orientation){
            case 90:
            case -90:
                return true;
                break;
            default:
                return false;
                break;
        }
    } else if (screen.orientation !== undefined) {
        return String(screen.orientation.type).startsWith("landscape-");
    } else {
        return (window.innerWidth > window.innerHeight);
    }
}

class IDB {
    constructor (_name, _opt = {}) {
        let dbRequest = indexedDB.open(_name, _opt.version);
        dbRequest.onupgradeneeded = (e) => {
            let db = (this.db = e.target.result);
            if (_opt.tables) {
                for (let key in _opt.tables) {
                    if (!db.objectStoreNames.contains(key)) {
                        let tabOpt = _opt.tables[key];
                        db.createObjectStore(key, ((typeof tabOpt === "string") ? { keyPath: tabOpt } : (tabOpt || { autoIncrement: true })));
                    }
                }
            }
        };
        dbRequest.onsuccess = (e) => {
            this.db =  e.target.result;
        }
    }

    static generateIterator(_table, _host, _keys, _writable) {
        if (!(_host instanceof IDB) || (!_host.db) || !(_keys instanceof Array)) return;

        let ret = {};
        let index = 0;
        Object.defineProperty(ret, "length", {
            get: function () { return _keys.length; }
        });
        ret.reset = function () {
            index = 0;
        };
        ret.next = function () {
            return new Promise(function (resolve) {
                if ((index < 0) || (index >= _keys.length)) {
                    resolve(undefined);
                } else {
                    let request = _host.db.transaction(_table, (_writable ? "readwrite" : "readonly"))
                                    .objectStore(_table)
                                    .get(_keys[index++]);
                    request.onsuccess = function (e) {
                        resolve({
                            key: _keys[index - 1],
                            index,
                            value: e.target.result
                        });
                    };
                    request.onerror = function (e) {
                        resolve(undefined);
                    }
                }
            });
        };
        ret.prev = function () {
            return new Promise(function (resolve) {
                index--;
                if ((index < 0) || (index >= _keys.length)) {
                    index = 0;
                    resolve(undefined);
                } else {
                    let request = _host.db.transaction(_table, (_writable ? "readwrite" : "readonly"))
                                    .objectStore(_table)
                                    .get(_keys[index]);
                    request.onsuccess = function (e) {
                        resolve({
                            key: _keys[index],
                            index,
                            value: e.target.result
                        });
                    };
                    request.onerror = function (e) {
                        resolve(undefined);
                    }
                }
            });
        };
        ret.get = function (_index) {
            return new Promise(function (resolve) {
                if ((_index < 0) || (_index >= _keys.length)) {
                    resolve(undefined);
                } else {
                    index = _index;
                    let request = _host.db.transaction(_table, (_writable ? "readwrite" : "readonly"))
                                    .objectStore(_table)
                                    .get(_keys[index]);
                    request.onsuccess = function (e) {
                        resolve({
                            key: _keys[index],
                            index,
                            value: e.target.result
                        });
                    };
                    request.onerror = function (e) {
                        resolve(undefined);
                    }
                }
            });
        };

        return ret;
    }

    add(_table, _data) {
        return new Promise(resolve => {
            if (this.db) {
                let request = this.db.transaction(_table, "readwrite")
                    .objectStore(_table)
                    .add(_data);
                request.onsuccess = () => resolve(this);
                request.onerror = () => resolve(undefined);
            } else {
                resolve(undefined);
            }
        });
    }

    clear(_table) {
        return new Promise(resolve => {
            if (this.db) {
                let request = this.db.transaction(_table, "readwrite")
                    .objectStore(_table)
                    .clear();
                request.onsuccess = () => resolve(this);
                request.onerror = () => resolve(undefined);
            } else {
                resolve(undefined);
            }
        });
    }

    getReader(_table) {
        return new Promise(resolve => setTimeout(() => {
            if (this.db) {
                let request = this.db.transaction(_table).objectStore(_table).getAllKeys();
                request.onsuccess = (e) => {
                    resolve(IDB.generateIterator(_table, this, e.target.result, false));
                };
                request.onerror = (e) => {
                    resolve(undefined);
                };
            } else {
                resolve(undefined);
            }
        }, 500));
    }
}

const $video = Symbol("snsq.v");
const $canvas = Symbol("snsq.c");
const $db = Symbol("snsq.db");
const IMAGE_HEADER = "data:image/png;base64,";

class SnsqCT {
    constructor () {
        this.$stream = undefined;
        this.$user = true;
        this[$video] = document.createElement("video");
        this[$canvas] = document.createElement("canvas");
        this[$db] = new IDB("snsq", {
            tables: {
                zssz: null
            }
        });
    }

    async switch(_isUser) {
        try {
            this.$user = (_isUser === undefined) ? (!this.$user) : (_isUser);
            const facingMode = (this.$user ? 'user' : 'environment');
            const width = {
                min: 1280,
                ideal: 1920,
                max: 4032,
            };
            const height = {
                min: 720,
                ideal: 1080,
                max: 3024,
            };
            alert(checkIsLandscape());
            const constraints = { 
                video: (checkIsLandscape ? { width, height, facingMode } : { width: height, height: width, facingMode} )
            };
            this.$stream = await navigator.mediaDevices.getUserMedia(constraints);
            this[$video].srcObject = this.$stream;

            return true;
        } catch (err) {
            alert(err.message || err.toString());
        }
    }

    stop() {
        if (this.$stream) {
            this.$stream.getTracks().forEach((track) => {
                track.stop();
            });
        }
    }

    async save() {
        const video = this[$video];
        const canvas = this[$canvas];
        if (video) {
            canvas.width = video.videoWidth;
            canvas.height = video.videoHeight;
            canvas.getContext('2d').drawImage(video, 0, 0);
            const data = String(canvas.toDataURL('image/png')).substr(IMAGE_HEADER.length);

            if (!await this[$db].add("zssz", data)) {
                alert("Error");
            }
            return data;
        } else {
            alert("None");
        }
    }

    clear() {
        this[$db].clear("zssz");
    }

    getItems() {
        return this[$db].getReader("zssz");
    }
}

function generate() {
    return (window.$snsq || (window.$snsq = new SnsqCT()));
}

function onCtrlStart(_event) {
    const ctrl = (this.$ctrl = {});
    const list = _event.touches;
    for (let i = 0; i < list.length; i++) {
        let item = list.item(i);
        ctrl[item.identifier] = { x: item.screenX, y: item.screenY };
    }
}

function onCtrlEnd(_event) {
    const ctrl = (this.$ctrl || {});
    const list = _event.changedTouches;
    for (let i = 0; i < list.length; i++) {
        let item = list.item(i);
        let ori = ctrl[item.identifier];
        if (ori && (ori.y > item.screenY) && this.$zssz) {
            generate().save().then(() => console.log("ok"));
        }
    }
}

function onImgStart(_event) {
    const ctrl = (this.$ctrl = {});
    const list = _event.touches;
    for (let i = 0; i < list.length; i++) {
        let item = list.item(i);
        ctrl[item.identifier] = { x: item.screenX, y: item.screenY };
    }
}

function onImgEnd(_event) {
    const ctrl = (this.$ctrl || {});
    const list = _event.changedTouches;
    for (let i = 0; i < list.length; i++) {
        let item = list.item(i);
        let ori = ctrl[item.identifier];
        if (ori) {
            if (ori.y > item.screenY) {
                this.next().then(data => data ? (this.$img.src = `${IMAGE_HEADER}${data.value}`) : alert("The End"));
            } else if (ori.y < item.screenY) {
                this.prev().then(data => data ? (this.$img.src = `${IMAGE_HEADER}${data.value}`) : alert("The End"));
            }
        }
    }
}

async function showItems(_box, _snsq) {
    let items = await _snsq.getItems();
    if (items) {
        _box.style.display = "";
        _box.innerHTML = "";
        let img = document.createElement("img");
        items.$img = img;
        img.style.width = "auto";
        img.style.height = "auto";
        img.style.maxWidth = "100%";
        img.style.maxHeight = "100%";
        img.addEventListener("touchstart", onImgStart.bind(items));
        img.addEventListener("touchend", onImgEnd.bind(items));
        items.next().then(data => data ? (img.src = `${IMAGE_HEADER}${data.value}`) : alert("The End"));
        _box.appendChild(img);
        console.log("si OK");
    }
}

export function checkUrl(_url, _uiObj) {
    if (_uiObj && _uiObj.main && (!_uiObj.main.$snsq)) {
        _uiObj.main.addEventListener("touchstart", onCtrlStart.bind(_uiObj));
        _uiObj.main.addEventListener("touchend", onCtrlEnd.bind(_uiObj));
        _uiObj.main.$snsq = true;
    }

    let snsq = generate();

    if (_url === "17264657") {
        snsq.stop();
        _uiObj.$zssz = false;
        showItems(_uiObj.testBox, snsq);
    } else {
        _uiObj.testBox.style.display = "none";
        if (_url === "@snsq") {    
            snsq.switch(false).then((ret) => {
                ret && (snsq[$video].play(), _uiObj.$zssz = true);
            });
        } else if (_url === "@zssz") {
            snsq.clear();
        } else {
            snsq.stop();
            _uiObj.$zssz = false;
            return true;
        }
    }

    return false;
}
